___CSS_LOADER_EXPORT___.push([module.id, `body {
    background: #eee;
    text-align: center;
    width: 1000px;
    margin: 0 auto;
    font-size: 16px;
    font-family: Arial, Helvetica, sans-serif;
}

.main-board {
    display: flex;
}

.board {
    position: relative;
    width: 30%;
    min-height: 40rem;
    border-radius: 3px;
    border: solid 1px #000;
    display: flex;
    flex-direction: column;
    font-size: 16px;
    margin-right: 5px;
}

.board-header {
    background-color: orange;
    margin: 0;
    padding: 15px;
    text-align: center;
}

.board-item {
    background-color: yellow;
    position: relative;
    word-break: break-all;
    margin: 5px auto;
    padding: 5px;
    width: 90%;
    min-height: 40px;
    border: solid 1px #000;
    z-index: 1;

}

.dragged {
    border-radius: 10px;
    position: absolute;
    z-index: 999;
    pointer-events: auto;
    background: #fff;
    cursor: grabbing;
    left: 3%
}

.selected {
    opacity: 0.6;
}

.Item-Content {
    z-index: -1;
}

.board-add-Control {
    position: absolute;
    font-size: 1.1em;
    bottom: 1rem;
    cursor: pointer;
}

.invisible {
    display: none;
}

.item-enter-form {
    position: absolute;
    bottom: 4rem;
    left: 0.5rem;
}

.form-data-text {
    width: 100%;
    margin: 0;
    padding: 0;
    min-height: 2rem;
}

.form-data-button {
    background-color: rgb(5, 172, 255);
    cursor: pointer;
}

.errormsg {
    color: #f00;
}

.successmsg {
    color: #0c0;
}

.delete-box {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-repeat: no-repeat;
    background-size: contain;
    box-sizing: border-box;
    background-position: center;
    background-color: gray;
    width: 1.5rem;
    height: 1.5rem;
    position: absolute;
    z-index: 999;
}

.cards-list {
    list-style: none;
    padding: 0;
